
import { defineComponent, ref, Ref } from "vue";

export default defineComponent({
  name: "CheckPage",
  setup() {
    const year: Ref = ref(2021);
    const inc: Function = () => {
      year.value += 1;
    };
    const dec: Function = () => {
      year.value -= 1;
    };

    return {
      year,
      inc,
      dec,
    };
  },
});
